import firebase from 'firebase';
import app from './firebase';

export const listAllAd = async () => {
  try {
    let querySnapshot = await app.firestore().collection('ads').get();
    const ad = querySnapshot.docs.map((doc) => {
      return {
        id: doc.id,
        ...doc.data()
      }
    })
    return ad
  } catch (err) {
    console.log(err);
  }
}


export const addAd = async (values,image) => {
  try {
    
    if(typeof(image)=="undefined"){
      return;
    }
    var metadata = {
      contentType: 'image/jpeg'
    };
    values.create_at = firebase.firestore.FieldValue.serverTimestamp();
    let adRef = await app.firestore().collection('ads').add(values);
    let ad = await adRef.get();
    let adData = ad.data();
    console.log("done ad:"+ad.id)
    let img_url = "public/images/ads/" + ad.id;
    var storageRef = app.storage().ref();
    var uploadTask = storageRef.child(img_url).put(image, metadata);
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
      function (snapshot) {
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log('Upload cover is ' + progress + '% done');
        switch (snapshot.state) {
          case firebase.storage.TaskState.PAUSED: // or 'paused'
            console.log('Upload cover is paused');
            break;
          case firebase.storage.TaskState.RUNNING: // or 'running'
            console.log('Upload cover is running');
            break;
        }
      },
      function (error) {

        switch (error.code) {
          case 'storage/unauthorized':
              console.log("unauthorized")
            break;
          case 'storage/canceled':
            console.log("cancel")
            break;
          case 'storage/unknown':

            console.log("unkonwn")
            // Unknown error occurred, inspect error.serverResponse
            break;
        }
      },
      function () {
        // Upload completed successfully, now we can get the download URL
        uploadTask.snapshot.ref.getDownloadURL().then(function (downloadURL) {
          console.log('File available at', downloadURL);
          adData.img = downloadURL;
          app.firestore().collection('ads').doc(ad.id).update(adData);
        });
      });
  } catch (err) {
    console.log(err);
  }
};

export const deleteAds = (ids) => {
  try {
    ids.map((id) => {
      let img_url = "public/images/ads/" + id;
      var storageRef = app.storage().ref();
      storageRef.child(img_url).delete();
      app.firestore().collection('ads').doc(id).delete();
    })
  } catch (err) {
    console.log(err);
  }
};

export const getAdApi = async (id) => {
  try {
    let querySnapshot = await app.firestore().collection('ads').doc(id).get();
    const ad = querySnapshot.data()
    ad.id = id
    return ad
  } catch (err) {
    console.log(err);
  }
}
