import React,{
  useState,
  useEffect,
  useCallback} from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  TextField,
  makeStyles,
  CardHeader,
  Divider,
  Tooltip
} from '@material-ui/core';
import FilesDropzone from 'src/components/FilesDropzone';
import wait from 'src/utils/wait';
import {addAd} from '../../../api/ads.js';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import { useHistory } from 'react-router-dom';
const useStyles = makeStyles(() => ({
  root: {}
}));

const AdCreateForm = ({
  className,
  ...rest
}) => {
  const classes = useStyles();
  const history = useHistory();
  const isMountedRef = useIsMountedRef();

  const [files, setFiles] = useState([]);


  const handleRemoveAll = () => {
    setFiles([]);
  };
  const handleDrop = useCallback((acceptedFiles) => {
    setFiles((prevFiles) => acceptedFiles);
  }, []);

  const { enqueueSnackbar } = useSnackbar();
    return (
    <Formik
    initialValues={{
      switch_time: '',
      tags: '',
      url: '',
      submit: null
    }}
      validationSchema={Yup.object().shape({
        tags: Yup.string().max(255),
        url: Yup.string().max(255),

      })}
      onSubmit={async (values, {
        resetForm,
        setErrors,
        setStatus,
        setSubmitting
      }) => {
        try {

          values.tags = values.tags.split(",")
          console.log(values)
          addAd(values,files[0])
          // NOTE: Make API request
          await wait(500);
          resetForm();
          setStatus({ success: true });
          setSubmitting(false);
          enqueueSnackbar('Ad updated', {
            variant: 'success',
            action: <Button>See all</Button>
          });

          history.push('/hbon/management/ads');
        } catch (err) {
          console.error(err);
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        touched,
        values
      }) => (
        <form
          className={clsx(classes.root, className)}
          onSubmit={handleSubmit}
          {...rest}
        >
          <Card>
            <CardContent>
              <Grid
                container
                spacing={3}
              >
                <Grid
                  item
                  md={6}
                  xs={12}
                >
                  <TextField
                    error={Boolean(touched.switch_time && errors.switch_time)}
                    fullWidth
                    helperText={touched.switch_time && errors.switch_time}
                    label="Thời gian hiển thị(s)"
                    name="switch_time"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.switch_time}
                    variant="outlined"
                  />
                </Grid>
                <Grid
                  item
                  md={6}
                  xs={12}
                >
                  <Tooltip title="Viết liền, không dấu, cách bởi dấu ," disableHoverListener>
                  <TextField
                    error={Boolean(touched.tags && errors.tags)}
                    fullWidth
                    helperText={touched.tags && errors.tags}
                    label="tags"
                    name="tags"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.tags}
                    variant="outlined"
                  />
                  </Tooltip>
                </Grid>
                <Grid
                  item
                  md={6}
                  xs={12}
                >
                  <Tooltip title="Định dạng url" disableHoverListener>
                  <TextField
                    error={Boolean(touched.url && errors.url)}
                    fullWidth
                    helperText={touched.url && errors.url}
                    label="Target url"
                    name="url"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.url}
                    variant="outlined"
                  />
                  </Tooltip>
                </Grid>
                
              </Grid>
              <Box mt={3}>
                <Card>
                  <CardHeader title="Tải ảnh lên" />
                  <Divider />
                  <CardContent>
                    <FilesDropzone handleDrop = {handleDrop} files={files} handleRemoveAll={handleRemoveAll}/>
                  </CardContent>
                </Card>
              </Box>
              <Box mt={2}>
                <Button
                  variant="contained"
                  color="secondary"
                  type="submit"
                  disabled={isSubmitting}
                >
                  Lưu
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
};

AdCreateForm.propTypes = {
  className: PropTypes.string
};

export default AdCreateForm;
